.wrapper {
  padding: 15vh 5vw;
  min-width: calc(90vw - 36px);
  min-height: 70vh;
  max-width: 100vw;
  overflow-x: hidden; }
  .wrapper ::selection {
    background-color: var(--c-acnt);
    color: var(--c-fore); }
  .wrapper a, .wrapper a:visited {
    color: inherit;
    text-decoration: underline;
    max-width: 54vw;
    text-overflow: ellipsis;
    display: inline-block;
    overflow: hidden;
    vertical-align: bottom; }
    .wrapper a, .wrapper a *, .wrapper a:visited, .wrapper a:visited * {
      cursor: pointer; }
    .wrapper a[data-mono], .wrapper a:visited[data-mono] {
      font-family: 'Fira Mono', monospace; }
    .wrapper a[data-mono="slide"], .wrapper a:visited[data-mono="slide"] {
      position: relative;
      transition: text-shadow 0.8s, color 0.4s;
      padding: 1px 3px; }
      .wrapper a[data-mono="slide"]::before, .wrapper a:visited[data-mono="slide"]::before {
        content: '';
        background-color: var(--c-fore);
        width: 0px;
        height: 100%;
        display: block;
        position: absolute;
        top: 0;
        right: 0;
