        border-radius: 3px; }
      .wrapper a[data-mono="slide"]:hover, .wrapper a[data-mono="slide"]:focus, .wrapper a:visited[data-mono="slide"]:hover, .wrapper a:visited[data-mono="slide"]:focus {
        color: var(--c-back);
