        outline: none; }
        .wrapper a[data-mono="slide"]:hover::before, .wrapper a[data-mono="slide"]:focus::before, .wrapper a:visited[data-mono="slide"]:hover::before, .wrapper a:visited[data-mono="slide"]:focus::before {
          left: 0;
          right: 0;
          width: 100%; }
  .wrapper img {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
  .wrapper span {
    font-size: 11pt;
    font-weight: 400; }
  .wrapper [data-visible="opaque"] {
    opacity: 1; }
  .wrapper [data-visible="translucent"] {
    opacity: 0.4; }
  .wrapper [data-visible="transparent"] {
    opacity: 0; }
  .wrapper .header {
