.wrapper {
  padding: 15vh 5vw;
  min-width: calc(90vw - 36px);
  min-height: 70vh;
  max-width: 100vw;
  overflow-x: hidden; }
  .wrapper ::selection {
    color: var(--c-fore);
    background-color: var(--c-acnt); }
  .wrapper .hidden {
    visibility: hidden; }
  .wrapper .acnt {
    color: var(--c-acnt); }
  .wrapper a {
    color: var(--c-fore); }
    .wrapper a:hover, .wrapper a:focus {
      color: var(--c-acnt); }
